body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#search{
  display: block;
  margin-left: auto;
  margin-right: auto;

}
form{
  width: 100%;
  margin: 10px;
  font-size: 30px;
  text-align: center;
}

select{
  margin-left:20px;
  font-size: 20px;
}

#myBtn{
  display: block;
  margin-left: auto;
  margin-right: auto;
}

h1{
  color:white;
  text-align: center;
  font-size: 50px;
  margin: 5px;
  font-family: Verdana, serif;
}
h2{
  font-family: Verdana, serif;
  margin: 5px;
  color:white;
  text-align: center;
  font-size: 20px;
  padding-bottom: 20px;
}
h3{
  font-family: Verdana, serif;
  margin: 5px;
  text-align: center;
}
.data_name{
  margin-bottom: 30px;
}
.icon{
  color:white;
  font-size: 5em;
  padding-top: 20px;
  display: -webkit-flex;
  display: flex;
   -webkit-justify-content: center;
           justify-content: center;

}
table{
  margin: auto;
  margin-right: 13%;
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 80%;

}
td{
  font-family: Verdana, serif;
  padding-top: 10px;
  width: 20%;
  font-size: 20px;
  text-align: center;
}
th{
  font-family: Verdana, serif;
  padding-top: 25px;
  width: 20%;
  font-size: 30px;
  text-align: center;
}

.head{
  background-color: #282E39;
}
button{
  padding: 8px;
  font-size: 25px;
}
#s_bar{
  font-size: 20px;
  width:70%;
  padding: 10px;
  border-style: solid;
  border-width: 2px;

}

.data{
  border-style:10px;
}
#bar{
  margin: auto;
  width: 100%;
  text-align: center;
  padding-top:5px;
}

